
import { defineComponent } from 'vue'
import HisTable from "@/components/DataViews/HisBasicTable.vue"
import ViewPort from "@/components/DataViews/ViewPort.vue"
import { isEmpty } from "lodash"
import FieldMixinVue from './FieldMixin.vue'

export default defineComponent({
    components: { ViewPort, HisTable },
    mixins: [FieldMixinVue],
    data: ()=>({
        isInit: false as boolean,
        data: {} as any,
        columns: [] as Array<string>,
        rows: [] as Array<string>,
        rowColors: [] as Array<any>,
        cellColors: [] as Array<any>,
        styles: [] as Array<string>
    }),
    watch: {
        data: {
            handler(d: any) {
                if (isEmpty(d)) {
                    this.$emit('onValue', null)
                    return this.rows = []
                }
                const [ option ] = d
                this.columns = option.other.columns
                this.rows = option.other.rows
                this.rowColors = option.other.rowColors
                this.cellColors = option.other.cellColors
                this.styles = this.config ? this.config.styles : []
                this.$emit('onValue', d)
            },
            deep: true
        }
    },
    methods: {
        clearValue() {
            this.data = []
        },
        async init() {
            this.$emit('onFieldActivated', this)
            this.data = await this.options(this.fdata, this.cdata, this)
        }
    },
    mounted() {
        this.init()
    },
    activated() {
        // This condition prevents double initialisation of the init function
        // when both mounted and activated hooks are present
        if (!this.isInit) {
            this.isInit = true
        } else {
            this.init()
        }
    }
})
